.episodes {
  padding-bottom: 260px;

  @include small-tablet {
    padding-bottom: 160px;
  }

  &__container {
    flex-direction: column;
  }

  &__top {
    justify-content: space-between;
    padding-bottom: 64.44px;

    @include mobile {
      flex-wrap: wrap;
      row-gap: 40px;
      justify-content: start;
    }
  }

  &__title {
    margin: 0;
    font-weight: 400;
    color: var(--light-color);
    font-size: 64px;
    line-height: 79.2px;
    letter-spacing: 2.88px;

    @include mobile {
      font-size: 30px;
      line-height: 60px;
    }
  }

  &__button {
    flex-shrink: 0;
    align-self: flex-end;
    padding: 12px 15px 12px 20px;

  }

  &__list {
    display: flex;
    flex-direction: column;
    row-gap: 60px;
    counter-reset: number;
  }

  &__item {
    display: flex;
    padding: 40px;
    background-color: #000;
    border-radius: 32px;
    column-gap: 53px;

    @include mobile {
      justify-content: center;
    }
  }

  &__image {
    flex: 0 1 33.333%;
    display: flex;

    @include meduim-tablet {
      display: none;
    }
  }

  &__text {
    flex: 0 1 66.666%;

    @include meduim-tablet {
      flex: 0 1 100%;
    }
  }

  &__category {
    background-color: #191919;
    border-radius: 6px;
    padding: 0 16px;
    font-size: 18px;
    line-height: 27px;
    margin-bottom: 48px;
  }

  &__subtitle {
    margin-bottom: 18px;
    counter-increment: number -1;
    color: var(--accent-color);
    font-size: 17px;
    line-height: 27px;

    &::after {
      content: counter(number);
      font-size: 18px;
      padding-left: 5px;
    }
  }

  &__title-item {
    color: var(--light-color);
    font-size: 64px;
    line-height: 70.4px;
    letter-spacing: 2.56px;
    margin-bottom: 28px;

    @include mobile {
      font-size: 40px;
      line-height: 45px;
    }
  }

  &__descr {
    color: rgba(255, 255, 255, 0.70);
    font-size: 22px;
    line-height: 33px;
    margin-bottom: 18px;

    @include mobile {
      display: none;
    }
  }
}

.episodes__category.focus-visible {
  outline: 2px solid var(--active-color);
  border-radius: 10px;
}

.episodes__button.focus-visible {
  outline: 2px solid var(--active-color);
  border-radius: 10px;
}
