.contact {
  padding: 0 50px;
  padding-bottom: 200px;

  @include mobile {
    padding: 0 20px;
    padding-bottom: 100px;
  }

  &__container {
    position: relative;
    padding: 0 60px;
    border-radius: 32px;
    background: #000;

    @include small-tablet {
      flex-direction: column;
      align-items: center;
    }

  }

  &__left {
    padding-top: 59.13px;
    padding-bottom: 57.12px;
    flex: 0 0 50%;
  }

  &__subtitle {
    color: #118DA8;
    font-size: 18px;
    line-height: 27px;

    @include mobile {
      font-size: 16px;
    }
  }

  &__title {
    margin: 0;
    font-weight: 400;
    color: var(--light-color);
    font-size: 72px;
    line-height: 79.2px;
    letter-spacing: 2.88px;

    @include mobile {
      font-size: 45px;
      line-height: 55px;
    }
  }

  &__form {
    width: 100%;
    flex: 0 1 auto;
    padding-top: 60px;
    padding-bottom: 75.12px;
    padding-left: 67px;
    flex-direction: column;
    display: flex;

    @include small-tablet {
      padding: 0;
      padding-bottom: 75px;
    }

    input {
      width: 522px;
      width: 100%;
      min-height: 82px;
      margin-bottom: 20px;
      border-radius: 6px;
      border: 1px solid #191919;
      background: #191919;
      color: rgba(255, 255, 255, 0.70);
      font-size: 24px;
      font-weight: 400;
      line-height: 28.8px;
      padding-left: 19px;

      &:focus {
        outline: 2px solid var(--light-color);
        outline-offset: 3px;
      }
    }
  }

  &__button {
    border-radius: 6px;
    max-width: 87px;
    min-height: 48px;
  }

  &__decor {
    position: absolute;
    top: 322.13px;
    left: -38px;

    @include small-tablet {
      display: none;
    }
  }
}
