.header {
  align-items: center;
  padding-top: 43px;
  padding-bottom: 83px;
}

.header__container {
  align-items: center;
  justify-content: space-between;
}

.nav {

  &__list {

    @include small-tablet {
      flex-direction: column;
      margin-right: 0;
      padding: 40px;
    }
  }

  &__item {
    margin-right: 40px;

    :last-child {
      margin: 0;
    }

    @include small-tablet {
      margin-bottom: 20px;
    }
  }

  &__link {
    color: #FFF;
    font-size: 17px;
    line-height: 27px;
    transition: color 0.3s ease-out;

    &:focus {
      outline: none;
    }

    @include hover {
      color: var(--accent-color);
    }

    &:active {
      color: var(--active-color);
    }

    @include small-tablet {
      font-size: 24px;
      line-height: 30px;
    }
  }

  @include small-tablet {
    position: fixed;
    left: 0;
    top: 0;
    width: 70%;
    height: 100vh;
    background-color: #141414;
    z-index: 100;
    display: flex;
    flex-direction: column;
    overflow-y: auto;
    transform: translateX(-100%);
    visibility: hidden;
    transition: transform 0.3s ease-in-out, visibility 0.3s ease-in-out;
  }
}

.nav__link.focus-visible {
  outline: 2px solid var(--active-color);
  border-radius: 10%;
  outline-offset: 3px;
}

.nav.menu--active {
  visibility: visible;
  transform: none;
}
