.footer {
  padding-bottom: 116px;

  &__container {

    @include for-desktop {
      justify-content: space-between;
    }

    @include tablet {
      flex-wrap: wrap;
      justify-content: initial;
      align-items: center;
    }
  }

  &__left {
    margin-right: 239px;
    padding: 0 20px;

    img {
      min-height: 46px;
      min-width: 224px;
    }

    @include for-desktop {
      margin-right: 0;
    }

    @include tablet {
      flex: 0 1 50%;
      margin: 0;
      margin-bottom: 50px;
      align-self: flex-start;
      order: 1;
    }

    @include mobile {
      flex: 0 1 100%;
    }
  }

  &__social-list {
    display: flex;
  }

  &__social-item {
    margin-right: 10px;

    &:last-child {
      margin: 0;
    }
  }

  &__social-link {

    img {
      min-height: 24px;
      min-width: 24px;
    }
  }

  &__center {
    display: flex;
    column-gap: 16px;
    margin-right: 21px;

    @include tablet {
      margin: 0;
      display: grid;
      align-content: center;
      grid-template-columns: repeat(8, 1fr);
      gap: var(--offset);
      order: 3;
      grid-auto-flow: dense;
    }

    @include mobile {
      grid-template-columns: repeat(4, 1fr);
    }
  }

  &__column-nav {
    padding-right: 104px;

    @include tablet {
      padding: 0;
    }

    @include mobile {
    }
  }

  &__item {
    margin-bottom: 10px;

    &:last-child {
      margin: 0;
    }
  }

  &__link {
    color: #FFF;
    font-size: 17px;
    line-height: 27px;
    transition: color 0.3s ease-in-out;

    &:focus {
      outline: none;
    }

    @include hover {
      color: var(--accent-color);
    }

    &:active {
      color: var(--active-color);
    }
  }

  &__column-credit {

    @include tablet {
      padding: 0;
      grid-column: 8;
    }

    @include mobile {
      grid-column: 4;
    }
  }

  &__right {
    @include tablet {
      flex: 0 1 50%;
      order: 2;
      display: flex;
      justify-content: flex-end;
      align-self: flex-start;
    }

    @include mobile {
      flex: 0 1 100%;
      padding: 0 20px;
      justify-content: flex-start;
      margin-bottom: 30px;
    }
  }

  &__list-music {
    display: flex;

  }

  &__item-music {
    margin-right: 16px;

    &:last-child {
      margin: 0;
    }
  }

  &__link-music {

    img {
      min-height: 32px;
      min-width: 32px;
    }
  }
}

.footer__social-link.focus-visible {
  outline: 2px solid var(--active-color);
  border-radius: 10%;
  outline-offset: 3px;
}

.footer__link.focus-visible {
  outline: 2px solid var(--active-color);
  border-radius: 10%;
  outline-offset: 3px;
}

.footer__link-music.focus-visible {
  outline: 2px solid var(--active-color);
  border-radius: 10%;
  outline-offset: 3px;
}

