.hero {
  padding-bottom: 93px;

  &__container {
    column-gap: 60px;
    padding: 0 40px;

    @include small-tablet {
      flex-direction: column;
      align-items: center;
    }
  }
  &__picture {
    position: relative;
    flex: 0 1 50%;

    &-sticks {
      position: absolute;
      top: 0;
      left: 0;
      top: -26px;
      left: -26px;
    }

    @include tablet {
      margin-bottom: 20px;
    }
  }

  &__right {
    flex-direction: column;
    justify-content: center;
    flex: 0 1 45%;
  }

  &__title {
    margin: 0;
    margin-bottom: 90px;

    &-default {
      color: var(--light-color);
      font-size: 70px;
      font-weight: 400;
      line-height: 88px;
      letter-spacing: 3.2px;

      @include tablet {
        font-size: 60px;
        line-height: 70px;
      }

      @include meduim-tablet {
        line-height: 65px;
        font-size: 50px;
      }
    }

    &-next {
      position: relative;
      color: var(--light-color);
      font-size: 75px;

      &-line {
        position: absolute;
        top: 0;
        left: 0;
        margin-top: 11px;
      }

      @include tablet {
        font-size: 65px;
      }


      @include meduim-tablet {
        font-size: 55px;
      }
    }

    &-bold {
      color: var(--light-color);
      font-size: 80px;
      font-weight: 700;

      @include tablet {
        font-size: 70px;
      }

      @include meduim-tablet {
        font-size: 60px;
      }
    }

  }

  &__listen {
    color: var(--light-color);
    font-size: 15px;
    line-height: 24px;

    @include tablet {
      font-size: 20px;
    }
  }

  &__text {
    margin: 0;
    margin-bottom: 14px;
  }

  &__list {
    display: flex;
  }

  &__item {
    margin-right: 17px;

    &:last-child {
      margin: 0;
    }
  }
}
