.about {
  padding-bottom: 201px;

  @include small-tablet {
    padding-bottom: 101px;
  }

  &__container {
    column-gap: 60px;

    @include tablet {
      flex-direction: column;
      align-items: center;
    }
  }

  &__left {
    display: flex;
    flex-direction: column;
    justify-content: center;
    flex: 0 1 45%;

    @include tablet {
      flex: 0 1 100%;
      order: 2;
      padding: 0 60px;
    }

    @include small-tablet {
      padding: 0;
    }
  }

  &__button {
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    border-radius: 100%;
    width: 112px;
    height: 112px;
    background-color: #118DA8;
    border-color: #118DA8;
    margin-bottom: 20px;
    transition: background-color 0.3s ease-in-out;

    &:focus {
      outline: none;
    }

    @include hover {
      background-color: var(--accent-color);
    }

    &:active {
      background-color: var(--active-color);
    }
  }

  &__subtitle {
    color: #118DA8;
    font-size: 18px;
    line-height: 27px;
  }

  &__title {
    margin: 0;
    margin-bottom: 28px;
    color: #FFF;
    font-size: 65px;
    line-height: 79.2px;
    letter-spacing: 2.88px;
    font-weight: 400;
  }


  &__descr {
    color: rgba(255, 255, 255, 0.70);
    font-size: 22px;
    font-weight: 400;
    line-height: 33px;

    p {
      margin: 0;
      margin-bottom: 33px;
    }
  }

  &__picture {
    flex: 0 1 50%;

    @include tablet {
      flex: 0 1 100%;
      order: 1;
      margin-bottom: 40px;
    }
  }
}

.about__button.focus-visible {
  outline: 3px solid var(--active-color);
}
